import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form, Input,
  // Divider, 
  Row, Col, Alert
} from "antd";

//const { Option } = Select;
const { TextArea } = Input;

class SupplierTypeUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { supplierType } = this.props;
    const { loading, loadingSubmit } = supplierType;
    const data = supplierType.supplierType ? supplierType.supplierType : [];

    const used = data.is_used;
    const error = supplierType.errorField ? supplierType.errorField : [];
    //console.log(used);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="supplierType.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
            ""
          )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="supplierTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="supplierTypes.add.code" />}
              validateStatus={
                error.supplier_type_code || getFieldError("supplier_type_code")
                  ? "error"
                  : null
              }
              help={
                error.supplier_type_code
                  ? error.supplier_type_code
                  : getFieldError("supplier_type_code")
              }
            >
              {getFieldDecorator("supplier_type_code", {
                initialValue: data.supplier_type_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                //   style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="supplierTypes.add.name" />}
            >
              {getFieldDecorator("supplier_type_name", {
                initialValue: data.supplier_type_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="supplierTypes.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description
              })(<TextArea autosize={{ minRows: 2 }} />)}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedSupplierTypeUpdateForm = Form.create({
  name: "update_supplier_type"
})(SupplierTypeUpdateForm);

export default WrappedSupplierTypeUpdateForm;
