import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import SupplierTypeUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/supplier/supplierType/actions";
import { Box } from "../../../App/app.style";

class UpdateSupplierType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editSupplierType } = this.props;
    const { id } = this.props.match.params;
    editSupplierType(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.supplierType.error) {
      notification("error", this.props.supplierType.error);
      this.props.clearError();
    }

    if (this.props.supplierType.success) {
      notification("success", this.props.supplierType.success);
      this.props.clearSuccess();
      const { editSupplierType } = this.props;
      const { id } = this.props.match.params;
      editSupplierType(id);
    }
  }

  handleSubmit = value => {
    const { updateSupplierType } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing supplier type code
    // value.supplier_type_code = value.supplier_type_code.toUpperCase();
    updateSupplierType(id, value);
  };

  render() {
    // console.log(this.props.supplierType);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="supplierTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={this.props.history.goBack}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.supplierType.loading &&
            !this.props.supplierType.loadingSubmit ? (
              <div className="spinStyle">
                <Spin size="large" />
              </div>
            ) : (
              <SupplierTypeUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                supplierType={this.props.supplierType}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, supplierType: state.supplierType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSupplierType);
